import React, { Component } from 'react'
import angular from '../images/Angular.svg.png'
import vue from '../images/Vue.svg.png'
import react from '../images/React.svg.png'
import me from '../images/mypic.jpg'


export default class HomePage extends Component {
    render() {
        return (
            <div>
                <div id="carouselExampleControls" class="carousel slide " data-ride="carousel">
                    <div class="carousel-inner">
                        <div class="carousel-item active">
                            <a href="https://angular.io/docs">
                            <img class="d-block w-100" src={angular} alt="First slide" height="400" /></a>
                        </div>
                        <div class="carousel-item">
                            <a href="https://reactjs.org/docs/getting-started.html">
                                <img class="d-block w-100" src={react} alt="Second slide" height="400" /></a>

                        </div>
                        <div class="carousel-item">
                            <a href="https://vuejs.org/v2/guide/">
                                <img class="d-block w-100" src={vue} alt="Third slide" height="400" />
                            </a>

                        </div>
                    </div>
                    <a class="carousel-control-prev" href="#carouselExampleControls" role="button" data-slide="prev">
                        <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                        <span class="sr-only">Previous</span>
                    </a>
                    <a class="carousel-control-next" href="#carouselExampleControls" role="button" data-slide="next">
                        <span class="carousel-control-next-icon" aria-hidden="true"></span>
                        <span class="sr-only">Next</span>
                    </a>
                </div>
            <div class="card m-5 myinfo">
            <div class="row no-gutters">
              <div class="col-md-4">
                <img src={me} class="card-img" alt="..." />
              </div>
              <div class="col-md-8">
                <div class="card-body">
                  <h1 class="card-title text-center"><strong>Enas Salah</strong></h1>
                  <p class="card-text">.Net Full Stack Developer
                  <br></br> ITI Assiut Student</p>
                  <p class="card-text"><small>enas.salah.b@gmail.com<br/> Sadat St. Mallawi Minia</small></p>
                </div>
              </div>
            </div>
          </div>
          
          </div>
        
      

        )
    }
}
