import React, { Component } from 'react'
import PropTypes from 'prop-types'
import axios from 'axios';
import { Link } from 'react-router-dom'

export default class UsersPage extends Component {
    constructor(props) {
        super(props)

        this.state = {
            users: [],
            errorMSG: ''
        }
    }
    componentDidMount() {
        axios.get('https://reqres.in/api/users')
            .then(response => {
                console.log(response);
                this.setState({
                    users: response.data.data


                })
            }).catch(error => {
                this.setState({ errorMSG: 'Error while reteriving data' })
            }
            );
    }
    render() {
        let usersData = this.state.users;
        let errorMSGData = this.state.errorMSG;
        return (
            //  <div className="d-flex flex--wrap justify-content-around card-deck">
             <div>
                <div className=" justify-content-center p-5">
                    <div className="card-deck"> 
                        {usersData.map(user =>
                        <div className="col-xl-3 pb-5 col-md-6 ">

                            <div class="card">
                                <img src={user.avatar} class="card-img-top" alt="" />
                                <div class="card-body ">
                                    {/* <h4 class="card-title">Name:{user.first_name} {user.last_name}</h4> */}

                                    <h5 class="card-text"><Link to={`/UserProfile/${user.id}`} >Email:{user.email}</Link></h5>
                                    <Link to={`/UserProfile/${user.id}`} ><button type="button" class="btn btn-warning">Details</button></Link>
                                </div>
                                <div class="card-footer">
                                    <small class="text-muted">Last updated 3 mins ago</small>
                                </div>
                            </div>
                            </div>)}
                        {errorMSGData ? <div>{errorMSGData}</div> : null}
                        {usersData.entries[0]}
                    </div>
                 </div>
             </div>
        )
    }
}
