//     return (
//         <div>
//                   <h3>ID: {id}</h3>

//         </div>
//     )
// }


export default class UserProfile extends Component {
    constructor(props) {
        super(props)

        this.state = {
            user: {},
            errorMSG: '',
        }
    }
    componentDidMount() {
        // let { id } = useParams();
        const { match: { params } } = this.props;
        var id=params.id;
        axios.get('https://reqres.in/api/users/'+id)
            .then(response => {
                console.log(response);
                this.setState({
                    user: response.data.data
                })
            }).catch(error => {
                this.setState({ errorMSG: 'Error while reteriving data' })
            }
            );
    }
    render() {
        let data=this.state.user;
        let errorMSGData = this.state.errorMSG;

        return (
            <div>
                <div class="card m-5 p-5 no-border myinfo">
            <div class="row no-gutters ">
              <div class="col-md-2">
                <img src={data.avatar} class="card-img" alt="..." />
              </div>
              <div class="col-md-6">
                <div class="card-body">
                         <h1 class="card-title text-center"><strong>{data.first_name} {data.last_name}</strong></h1>
                         <p class="card-text">{data.email}</p>
                </div>
              </div>
            </div>
          </div>
                        {errorMSGData ? <div>{errorMSGData}</div> : null}
            </div>
        )
    }
}
